import React from "react";
import OnlineUtils from "eshop/utils/OnlineUtils";

export default class ProducerFilterView extends React.Component {
    constructor(props) {
        super(props);
        this.handleProducerChooseAction = this.handleProducerChooseAction.bind(this);
    }

    handleProducerChooseAction(event, producer){
        event.preventDefault();
        this.props.setSelectedProducer(producer);
        this.props.reloadTree();
        this.props.reloadMatchToFilter();
        OnlineUtils.resetUrlPathWithoutReload();
    }
    componentDidUpdate(){
        // ToDo FIXME!!! wiem, ze boli...
        if($("#react-modal-ora-filters-modal").hasClass("is-visible"))
            OPL.UI.initModules(document.getElementById("react-modal-ora-filters-modal"));
        if(document.getElementById("opl-producer-filter-modal") && !document.getElementById("opl-producer-filter-modal").getAttribute("data-js-processed")) {
            OPL.UI.stopModules(document.getElementById("react-modal-ora-filters-modal"));
            OPL.UI.initModules(document.getElementById("react-modal-ora-filters-modal"));
        }
    }

    prepareProducerName(producer) {
        return producer.cuteName ? producer.cuteName : producer.name;
    }

    render() {
        return (
            <div id={"opl-producer-expanders" + (this.props.id ? ("-" + this.props.id) : "")}>
                <div id={this.props.id || "opl-producer-expander-main"} data-js-module="common/modules/opl-expander"
                     data-js-conditions="element:{was seen}"
                     data-js-options={JSON.stringify({
                         scrollToSelected: false,
                         triggerSelector: ".opl-section__trigger",
                         "contentSelector": ".opl-section__content",
                         "parentClass": "opl-section",
                     })}
                     role="tablist" aria-multiselectable="false">
                    <div className={"opl-section u-no-padding" + (this.props.initiallyExpanded ? " is-expanded" : "")}
                         aria-expanded={(this.props.initiallyExpanded ? "true" : "false")}>
                        <div className="opl-section__header u-no-padding">
                            <div className="l-row">
                                <div className="l-col l-col-9 ">
                                    <h4 className="h4 u-spacing-l">{this.props.title}</h4>
                                </div>
                                <div className="l-col l-col-3 ">
                                    <a href="#"
                                       className="u-text-right u-block opl-section__trigger opl-wiking-expander__trigger--plus"
                                       role="tab">
                                        <span className="g-icon g-icon--only g-icon--plus g-icon--xs"/>
                                        <span className="u-acc-hide">{"Rozwiń sekcję " + this.props.title}</span>
                                    </a>
                                </div>
                            </div>
                        </div>
                        <div className="l-row">
                            <div className="l-col l-col-12 ">
                                <div className="u-padding-right-xl">
                                    <ul className="opl-wiking-manufacturers">
                                        {this._renderProducer()}
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="opl-section__content u-hide--soft"
                             role="tabpanel">
                            <div className="l-row">
                                <div className="l-col l-col-12 ">
                                    <div className="u-padding-right-xl">
                                        <ul className="opl-wiking-manufacturers">
                                            {this._renderProducer(true)}
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    _renderProducer(hidden) {
        return this.props.filterConfiguration.producerFilter ? (this.props.filterConfiguration.producerFilter.map((producer, index) => {
            return (
                ((!hidden && index < 6) || (hidden && index > 5)) ? (
                    <li key={index} className="opl-wiking-manufacturers__wrapper"
                        onClick={(event) => this.handleProducerChooseAction(event, producer.name)}>
                        <div className="opl-wiking-manufacturers__item">
                            {!producer.iconUrl
                                ? this._renderTxtProducer(producer)
                                : this._renderImgProducer(producer)
                            }
                        </div>
                    </li>) : null
            )
        })) : null;
    }

    _renderImgProducer(producer){
        return (
            <a href={"/sklep/" + this.prepareProducerName(producer)} className={producer.name == this.props.selectedProducer ? "active" : ""}>
                <img src={producer.iconUrl} alt={producer.altText} title={producer.tooltip}/>
            </a>
        )
    }

    _renderTxtProducer(producer){
        return (
            <a href={"/sklep/" + this.prepareProducerName(producer)} className={producer.name == this.props.selectedProducer ? "active" : ""}>
                {producer.altText}
            </a>
        )
    }

}
